import React, { Component } from 'react';
import logo from './d_d_logo.jpg';
import {Link} from "react-router";

class Header extends Component
{

  render()
  {
    return (
      <div className='main'>
      <div>
    <Link to="/home">
      <img src={logo} alt="Logo" height='50px' />
      </Link>
          </div>
      </div>

    );
  }
}

export default Header;
